  font: 400 15px/20px Roboto,RobotoDraft, Helvetica, Arial, sans-serif;
}
#app {
  position: relative;
}
.background_style {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: grid;
  grid: repeat(5, 200px)/repeat(10, 1fr);
  background: -webkit-gradient(linear, left top, left bottom, from(#507eb1), color-stop(10%, #709dc7), color-stop(38%, #dde9f5), color-stop(48%, #eaf2f9), color-stop(62%, #f6f9fc));
