  -webkit-transform: skewY(-12deg);
          transform: skewY(-12deg);
  -webkit-transform-origin: 0;
          transform-origin: 0;
  z-index: -1;
}
.background_style span:nth-child(1) {
    grid-column: span 3;
}
.background_style span:nth-child(2) {
    grid-area: 3 / span 3 / auto / -1;
}
.background_style span:nth-child(3) {
    grid-row: 4;
    grid-column: span 5;
}
.background_style span:nth-child(5) {
    grid-area: auto/1/-1/-1;
    background: -webkit-gradient(linear, left top, right top, color-stop(80%, #fff), to(#f5fafd));
    background: linear-gradient(100grad, #fff 80%, #f5fafd);
}
@media (min-width: 670px) {
.background_style span:nth-child(1) {
    background: -webkit-gradient(linear, left top, right top, from(rgba(243, 251, 255, 0.4)), color-stop(20%, rgba(243, 251, 255, 0.15)), to(rgba(243, 251, 255, 0)));
    background: linear-gradient(100grad, rgba(243, 251, 255, 0.4), rgba(243, 251, 255, 0.15) 20%, rgba(243, 251, 255, 0));
}
.background_style span:nth-child(2) {
    background: -webkit-gradient(linear, left top, right top, from(rgba(0, 119, 204, 0)), color-stop(40%, rgba(0, 119, 204, 0.05)), to(rgba(0, 119, 204, 0.35)));
    background: linear-gradient(100grad, rgba(0, 119, 204, 0), rgba(0, 119, 204, 0.05) 40%, rgba(0, 119, 204, 0.35));
}
.background_style span:nth-child(3) {
    background: -webkit-gradient(linear, left top, right top, color-stop(50%, #fff), to(rgba(255, 255, 255, 0)));
    background: linear-gradient(100grad, #fff 50%, rgba(255, 255, 255, 0));
}
.background_style span:nth-child(4) {
    background: -webkit-gradient(linear, left top, right top, color-stop(10%, rgba(0, 119, 204, 0)), to(rgba(0, 119, 204, 0.05)));
    background: linear-gradient(100grad, rgba(0, 119, 204, 0) 10%, rgba(0, 119, 204, 0.05));
}
}



